nav {
  height: 3em;
  z-index: 10000;
  transform-style: preserve-3d;
  background: #fff;
  padding: 0 .6em;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0 0 .2em #aaa3;
}

nav .brand, nav .menu, nav .burger {
  float: right;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

nav .brand {
  float: left;
  max-width: 50%;
  white-space: nowrap;
  color: inherit;
  padding: 0 .6em;
  font-weight: 700;
}

nav .brand * {
  vertical-align: middle;
}

nav .logo {
  height: 2em;
  margin-right: .3em;
}

nav .select:after {
  height: calc(100% - 1px);
  padding: 0;
  line-height: 2.4em;
}

nav .menu > * {
  margin-right: .6em;
}

nav .burger {
  display: none;
}

@media (max-width: 60em) {
  nav .burger {
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    margin: 0;
    display: inline-block;
    bottom: -1000em;
  }

  nav .burger ~ .menu, nav .show:checked ~ .burger {
    min-height: 100%;
    background: #fff;
    margin: 0;
    transition: all .5s;
    position: fixed;
    top: 0;
    bottom: -1000em;
    right: 0;
    transform: none;
  }

  nav .burger ~ .menu {
    z-index: 11;
  }

  nav .show:checked ~ .burger {
    color: #0000;
    width: 100%;
    background: #1113;
    border-radius: 0;
    transition: all .5s;
  }

  nav .show ~ .menu {
    width: 70%;
    max-width: 300px;
    transform-origin: 100%;
    transition: all .25s;
    transform: scaleX(0);
  }

  nav .show ~ .menu > * {
    transition: all 0s .5s;
    transform: translateX(100%);
  }

  nav .show:checked ~ .menu > :nth-child(1) {
    transition: all .5s cubic-bezier(.645, .045, .355, 1);
  }

  nav .show:checked ~ .menu > :nth-child(2) {
    transition: all .5s cubic-bezier(.645, .045, .355, 1) .1s;
  }

  nav .show:checked ~ .menu > :nth-child(3) {
    transition: all .5s cubic-bezier(.645, .045, .355, 1) .2s;
  }

  nav .show:checked ~ .menu > :nth-child(4) {
    transition: all .5s cubic-bezier(.645, .045, .355, 1) .3s;
  }

  nav .show:checked ~ .menu > :nth-child(5) {
    transition: all .5s cubic-bezier(.645, .045, .355, 1) .4s;
  }

  nav .show:checked ~ .menu > :nth-child(6) {
    transition: all .5s cubic-bezier(.645, .045, .355, 1) .5s;
  }

  nav .show:checked ~ .menu {
    transform: scaleX(1);
  }

  nav .show:checked ~ .menu > * {
    transition: all .5s ease-in-out .6s;
    transform: translateX(0);
  }

  nav .burger ~ .menu > * {
    text-align: left;
    max-width: calc(100% - .6em);
    margin: .3em;
    display: block;
  }

  nav .burger ~ .menu > a {
    padding: .3em .9em;
  }
}

